const defaultState = {
  savedProblems: {}
};
const problems = (state = defaultState, action) => {
    const newState = {
      FETCH_PROBLEMS: {...state, savedProblems: action.payload},
      CREATE_PROBLEM: state,
      // CREATE_PROBLEM: createProblem,
      // UPDATE_PROBLEM: updateProblem,
    }[action.type];
    return newState || state;
};

export default problems;



