import React from 'react';
import MainTheme from './themes/mainTheme';
import Grid from 'material-ui/Grid';
import { withStyles } from 'material-ui/styles';
import PropTypes from 'prop-types';
import ProblemAppBar from './problemAppBar';
import TopMenu from './menus/topMenu';
import EditPage from './editPage';
import WelcomeScreen from './welcomeScreen';
import ProblemScreen from './problemScreen';
import SolutionScreen from './solutionScreen';
import Utils from '../utils';

const styles = theme => ({
  root: {
    flexGrow: 1,
    marginTop: 30
  },
  page:{
    marginTop: 15
  }
});

class App extends React.Component {
  constructor(props) {
    super(props);
    this.Screens = Object.freeze({
      "welcome":0,
      "savedProblem":1,
      "viewSavedProblem":2,
      "editor":3
    });
    this.state = {
      currentScreen: this.Screens.welcome,
      selectedProblemId: null,
      editorPage: null,
      editorProblem: null
    };
  }

  componentDidMount() {
    this.props.fetchProblems();
  }

  onEditorPageSelected = (event, value) => {
    this.setState({ editorPage: value });
  }

  onNewProblemSelected = () => {
    this.setState({
      currentScreen: this.Screens.editor,
      selectedProblemId: null,
      editorPage: 0,
      editorProblem: this.newProblem
    });
  }

  onProblemSelected = (id) => {
    this.setState({
      currentScreen: this.Screens.savedProblem,
      selectedProblemId: id,
      editorPage: null,
      editorProblem: null
    });
  }

  onViewProblemClicked = () => {
    this.setState({
      currentScreen: this.Screens.viewSavedProblem
    });
  }

  onProblemEdited = () => {
    this.setState({
      currentScreen: this.Screens.editor,
      editorPage: 0,
      editorProblem: Utils.cloneProblem(this.selectedProblem)
    });
  }

  onProblemDeleted = () => {

  }

  onEditorSave = (problem) => {
    if (!this.state.selectedProblemId) {
      const action = this.props.createProblem(problem);
      this.props.fetchProblems();
      this.setState({
        selectedProblemId: action.payload.id,
        editorProblem: Utils.cloneProblem(this.state.editorProblem)
      });
    } else {
      this.props.updateProblem(this.state.selectedProblemId, problem);
    }
  }

  onEditorChange = (changes) => {
    this.setState({
      editorProblem: Utils.cloneProblem(this.state.editorProblem, changes)
    });
  }

  isCurrenScreen = (screen) => {
    return this.state.currentScreen === this.Screens[screen];
  }

  get selectedProblem() {
    let selectedProblemId = this.state.selectedProblemId;
    if (selectedProblemId === null || selectedProblemId === undefined) {
      return null;
    } else {
      return this.props.problems[selectedProblemId];
    }
  }

  get newProblem() {
    const lastProblem = Utils.objectToArr(this.props.problems).sort((p1, p2) => {
      return p1.updatedAt >= p2.updatedAt ? -1 : 1;
    })[0] || {};

    return {
      description: "",
      verbs: [],
      transformationVerbs: [],
      pastVerbs: [],
      negativeVerbs: [],
      name: lastProblem.name || "",
      newName: "",
      pastDomino: lastProblem.pastDomino || "",
      futureDomino: lastProblem.futureDomino || "",
      updatedAt: null
    };
  }

  render() {
    const { classes } = this.props;
    const page = [
      <WelcomeScreen/>,
      <ProblemScreen
        problem={this.selectedProblem}
        onViewProblemClicked={this.onViewProblemClicked}
        onEdit={this.onProblemEdited}
        onDelete={this.onProblemDeleted}
      />,
      <SolutionScreen
        problem={this.selectedProblem}
      />,
      <EditPage
        selectedPage={this.state.editorPage}
        problem={this.state.editorProblem}
        onSave={this.onEditorSave}
        onChange={this.onEditorChange}
      />
    ][this.state.currentScreen];

    let topMenu;
    if (this.isCurrenScreen('editor')) {
      topMenu = (
        <TopMenu
          selectedPage={this.state.editorPage}
          onPageSelected={this.onEditorPageSelected}
        />
      );
    }
    return (
      <MainTheme>
        <div className={classes.root}>
          <Grid container spacing={8}>
            <Grid item xs={12}>
              <ProblemAppBar
                onProblemSelected={this.onProblemSelected}
                onNewProblemSelected={this.onNewProblemSelected}
                problems={this.props.problems}
                topComponent={topMenu}
              />
            </Grid>
            <Grid item xs={12} className={classes.page}>
              {page}
            </Grid>
          </Grid>
        </div>
      </MainTheme>
    );
  };
};

App.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(App);



